:root {
    --green: #72d276;
    --orange: #e4ab26;
    --white: #fff;
    --black: #000;
    --grey: #696969;
    --dark: #352d2d;
    --light: #f5f5f5;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

ol,
ul,
li {
    list-style: none;
}

a {
    text-decoration: none;
}

body {
    height: 100vh;
    font-family: system-ui;
    background-color: var(--grey);
    display: flex;
    justify-content: center;
    align-items: center;
}
